      if (!vm.lineGradient) {
        vm.lineGradient = ctx.createLinearGradient(0, 0, 0, CH);
        vm.lineGradient.addColorStop(0, '#aaaaaa');
        vm.lineGradient.addColorStop(0.5, '#aaaaaa');
        vm.lineGradient.addColorStop(0.9, '#333333');
        vm.lineGradient.addColorStop(1, '#fff');
      }
      var workingAudio = vm.workingAudio;

      if (!vm.actx) {
        try {
          vm.actx = new (window.AudioContext || window.webkitAudioContext || window.mozAudioContext)();
        } catch (e) {
          console.log('!Your browser does not support AudioContext');
        }
      }
      var actx = vm.actx;
      // volume control
      if (!workingAudio.gainNode) {
        workingAudio.gainNode = actx[actx.createGain ? 'createGain' : 'createGainNode']();
        workingAudio.gainNode.gain.value = 0.3;
        workingAudio.gainNode.connect(actx.destination);
      }
      var gainNode = workingAudio.gainNode;
      var barNum = 128;

      if (!workingAudio.analyser) {
        workingAudio.analyser = actx.createAnalyser();
        workingAudio.analyser.fftSize = 2 * barNum;
      }
      var analyser = workingAudio.analyser;

      if (!workingAudio.mediaSrc) {
        workingAudio.mediaSrc = actx.createMediaElementSource(workingAudio);
      }
      var mediaSrc = workingAudio.mediaSrc;
      mediaSrc.connect(analyser);
      analyser.connect(gainNode);

      // 短线Y坐标位置
      var dashYPositions = [];

      function visualization() {
        var arr = new Uint8Array(analyser.frequencyBinCount);
        requestAnimationFrame = window.requestAnimationFrame || window.webkitRequestAnimationFrame || window.mozRequestAnimationFrame;
        function v() {
          analyser.getByteFrequencyData(arr);
          drawBars(arr);
          requestAnimationFrame(v);
        }
        requestAnimationFrame(v);
      }
      // 绘制柱状音乐可视化效果
      function drawBars(arr) {
        ctx.clearRect(0, 0, CW, CH);
        var w = CW / barNum + 3;
        for (var i = 0; i < barNum; i++) {
          var value = arr[i];
          var h = value / 300 * CH;
          if (dashYPositions.length < barNum) {
            dashYPositions.push(h);
          }
          ctx.fillStyle = '#FFCC00';
          if (h < dashYPositions[i]) {
            ctx.fillRect(i * w, CH - --dashYPositions[i] - 7, w * 0.7, 1.5);
          } else {
            ctx.fillRect(i * w, CH - h - 7, w * 0.7, 1.5);
            dashYPositions[i] = h;
          }
          ctx.fillStyle = vm.lineGradient;
          ctx.fillRect(i * w, CH - h, w * 0.7, h);
        }
      }
      visualization();
