    typeText: function typeText(mystr, target, cb) {
      var vm = this;
      var mystrCopy = mystr;
      var len = mystr.length;
      var strArr = mystr.split('');
      var colors = ['#d00', '#00d', '#0d0', '#cc0', '#c0c', '#0cc', '#000'];
      var progress = 0;
      var timer = setInterval(function () {
        if (progress < len) {
          target.innerHTML = mystrCopy.substring(0, progress) + '<span id="cursor">' + (progress & 1 ? '_' : '') + '</span>';

          var random = Math.floor(Math.random() * colors.length);
          $('#cursor').css('color', colors[random]);
          progress++;
        } else {
          target.innerHTML += '<br>';
          $('#cursor').hide();
          window.clearInterval(timer);
          cb && cb.call();
        }
      }, 100);
    },
