        <p class="content">Auto Piano支持键盘按键和鼠标点击播放，同时琴键上会有按键和音名提示。AutoPiano的亮点是支持<span class="code">钢琴曲自动播放</span>，应用会根据计算机可识别格式的钢琴谱进行自动播放，例如右边列表中已经有一些歌曲了，你可以试一试！👻👻👻</p>
