        {id: 56, name: '#C6', keyCode: 'b76', key: '空<br>格<br>+<br>l', url: noteBaseUrl + 'b76.mp3', type: 'black'},
        {id: 57, name: '#D6', keyCode: 'b90', key: '空<br>格<br>+<br>z', url: noteBaseUrl + 'b90.mp3', type: 'black'},
        {id: 58, name: '#F6', keyCode: 'b67', key: '空<br>格<br>+<br>c', url: noteBaseUrl + 'b67.mp3', type: 'black'},
        {id: 59, name: '#G6', keyCode: 'b86', key: '空<br>格<br>+<br>v', url: noteBaseUrl + 'b86.mp3', type: 'black'},
        {id: 60, name: '#A6', keyCode: 'b66', key: '空<br>格<br>+<br>b', url: noteBaseUrl + 'b66.mp3', type: 'black'},
      ]
    }
  },
  methods: {
    // 钢琴初始化
    initPiano() {
      var vm = this;
      setTimeout(() => {
        vm.computeEleSize();
      }, 300)
      vm.initAudioDom();
      vm.preloadAudio();
      vm.bindKeyBoradEvent();
      window.onresize = vm.computeEleSize;
      window.onorientationchange = vm.computeEleSize

      var lyricIndex = Math.floor(Math.random() * vm.someLyrics.length);
      vm.randomLyric = '"' + vm.someLyrics[lyricIndex] + '"';

      // vm.testSheet1 = '1=C:5(0.5),5(0.5),6(1),5(1),1<(1),7(2),5(0.5),5(0.5),6(1),5(1),2<(1),1<(2),5(0.5),5(0.5),5<(1),3<(1),1<(1),7(1),6(2),4<(0.5),4<(0.5),3<(1),1<(1),2<(1),1<(2)'
    },
    computeEleSize() {
      var wkey_width = $('.piano-key-wrap').width() / 36;
      var wkey_height = wkey_width * 7;
      var bkey_height = wkey_height * 0.7;
      $('.piano-key-wrap').height(wkey_height);
      $('.bkey').height(bkey_height);
    },
    initAudioDom() {
      var vm = this
      for (let i = 0; i< vm.Notes.length; i++) {
        var note = vm.Notes[i]
        $('.audios-wrap').append(`<audio src='${note.url}' hidden='true' data-id='audio${i}' class='audioEle'>`);
      }
    },
    // 预加载音频资源
    preloadAudio() {
      var vm = this
      var preloadAudio = vm.$refs.preloadAudio
      // var preloadAudio = new Audio()
      var i = 0, len = vm.Notes.length, loaded = 0
      preloadAudio.src = vm.Notes[i].url
      preloadAudio.load()
      preloadAudio.oncanplaythrough = () => {
        loaded++
        var percent = Math.round(loaded / len * 100)
        vm.$emit('autoPianoLoadEvent', percent)
        if (i < len - 1) {
          ++i
          preloadAudio.src = vm.Notes[i].url
          preloadAudio.load()
        }
      }
    },
    bindKeyBoradEvent() {
      var vm = this;
      var pressedNote;
      var keyType = '';
      $(document).keydown((e) => {
          if (vm.isUserTyping) return
          var keyCode = e.keyCode;
          if (vm.DEV) console.log('keydown-keycode', keyCode);
          // 按住空格键，则启用黑色按键
          if (keyCode == 32) {
            vm.enableBlackKey = true;
          }
          if (vm.enableBlackKey) {
            keyCode = 'b' + keyCode;
          }
          var filternotes = vm.Notes.filter((note) => {
            return note.keyCode == keyCode;
          })
          if (filternotes.length > 0) {
            pressedNote = filternotes[0];
            vm.DEV && console.log('keydown-notename', pressedNote.name);
            keyType = pressedNote.type;
            if (keyType == 'white') {
              $(`[data-keyCode=${pressedNote.keyCode}]`).addClass('wkey-active');
            } else if (keyType == 'black') {
              $(`[data-keyCode=${pressedNote.keyCode}]`).addClass('bkey-active');
            }
            
            vm.playNote(pressedNote.url);
          }
      });
      $(document).keyup((e) => {
        if (vm.isUserTyping) return
        var keyCode = e.keyCode;
        // 松开空格键，则禁用黑色按键
        if (keyCode == 32) {
          vm.enableBlackKey = false;
        }

        $(`.wkey`).removeClass('wkey-active');
        $(`.bkey`).removeClass('bkey-active');
      })
    },
    // 点击按键播放
    clickNote(e, keyCode) {
      var vm = this;
      vm.DEV && console.log(e.target);

      var filternotes = vm.Notes.filter((note) => {
        return note.keyCode == keyCode;
      })
      if (filternotes.length > 0) {
        var pressedNote = filternotes[0];
        vm.playNote(pressedNote.url);
      }
    },
    playNote(url) {
      var vm = this
      console.log(url);
      if (!url || typeof url != 'string') return;
      var audios = $('.audioEle');
      for (let i = 0; i< audios.length; i++) {
        let audio = audios[i];
        if (audio.src.indexOf(url) > -1) {
          var cloneAudioNode = audio.cloneNode()
          cloneAudioNode.play()
          cloneAudioNode.remove()
          break;
        }
      }
    },
    // 点击音乐列表播放音乐
    playMusicByName(name) {
      var vm = this
      var targetMusic
      for (let k in vm.demoMusic) {
        var music = vm.demoMusic[k]
        if (music.name == name) {
          targetMusic = music
          break
        }
      }
      if (targetMusic) {
        vm.playTimers.forEach((timer) => {
          clearInterval(timer)
          timer = null
        })
        vm.playTimers.splice(0)
        let rhythm = targetMusic.rhythm
        let speed = targetMusic.speed
        if (vm.Rhythm[rhythm]) {
          vm.autoPlayMusic(vm.Rhythm[rhythm], targetMusic.mainTrack, speed)
          if (targetMusic.backingTrack) {
            vm.autoPlayMusic(vm.Rhythm[rhythm], targetMusic.backingTrack, speed)
          }
        }
      }
      vm.musicListShow = false
    },
    // 自动播放音乐 Auto！
    autoPlayMusic(rhythm, musicSheet, speed) {
      var vm = this;
      if (!speed) speed = 75
      var unit = (60 * 1000) / speed;
      var startTime = new Date();
      var i = 0;
      var songTime = 0;
      var pressedNote;

      function getNoteUrl(note) {
        var n = note.match(/[#b]*[0-7][\<\>]*/g)[0];
        if (!rhythm[n]) {
          return '';
        }
        var noteName = rhythm[n];
        var filternotes = vm.Notes.filter((n) => {
          return n.name == noteName;
        })
        if (filternotes.length > 0) {
          pressedNote = filternotes[0];
          var noteUrl = pressedNote.url;
          if (pressedNote.type == 'white') {
            // $(`[data-keyCode=${pressedNote.keyCode}]`).addClass('wkey-active');
          } else {
            // $(`[data-keyCode=${pressedNote.keyCode}]`).addClass('bkey-active');
          }
          $(`[data-keyCode=${pressedNote.keyCode}]`).addClass('auto-key-active');
        }
        return noteUrl;
      }
      function getNoteDurationByStamp(note, unit) {
        var timeStamp = 1;
        if ( note.match(/\([0-9.]*\)/g) ) {
          timeStamp = note.match(/\([0-9.]*\)/g)[0].replace(/[\)\(]/g, '');
        }
        return unit * +timeStamp;
      }

      function start() {
        var curTime = new Date();
        var delta = curTime - startTime;
        if (delta >= songTime) {
          // 播放下一个音符
          if (pressedNote) {
            // $(`[data-keyCode=${pressedNote.keyCode}]`).removeClass('wkey-active');
            // $(`[data-keyCode=${pressedNote.keyCode}]`).removeClass('bkey-active');
            $(`[data-keyCode=${pressedNote.keyCode}]`).removeClass('auto-key-active');
          }
          vm.playNote(getNoteUrl(musicSheet[i]));
          var note = musicSheet[i];
          songTime += getNoteDurationByStamp(note, unit);
          i++;
          if (i >= musicSheet.length) {
            setTimeout(() => {
              $(`.piano-key`).removeClass('auto-key-active');
            }, 1000)
            clearInterval(playTimer);
            return;
          }
        }
        // requestAnimationFrame(start);
      }
      // requestAnimationFrame(start);
      var playTimer = setInterval(() => {
        start();
      }, 20);
      vm.playTimers.push(playTimer)
    },
    // 可视化效果
    startAudioVisualEffect() {
        var vm = this;
        var canvas = vm.$refs.audioEffectCanvas;
        const CW = 100, CH = 70;
        canvas.width = CW;
        canvas.height = CH;
        if (!vm.ctx) {
          vm.ctx = canvas.getContext('2d');
        }
        var ctx = vm.ctx;
        ctx.clearRect(0, 0, CW, CH);
        if (!vm.lineGradient) {
          vm.lineGradient = ctx.createLinearGradient(0,0,0,CH);
          vm.lineGradient.addColorStop(0, '#aaaaaa');
          vm.lineGradient.addColorStop(0.5, '#aaaaaa');
          vm.lineGradient.addColorStop(0.9, '#333333');
          vm.lineGradient.addColorStop(1, '#fff');
        }
        var workingAudio = vm.workingAudio;

        if (!vm.actx) {
            try {
              vm.actx = new (window.AudioContext || window.webkitAudioContext || window.mozAudioContext)();
            } catch (e) {
               console.log('!Your browser does not support AudioContext');
            }
        }
        var actx = vm.actx;
        // volume control
        if (!workingAudio.gainNode) {
          workingAudio.gainNode = actx[actx.createGain?'createGain':'createGainNode']();
          workingAudio.gainNode.gain.value = 0.3;
          workingAudio.gainNode.connect(actx.destination);
        }
        var gainNode = workingAudio.gainNode;
        var barNum = 128;

        if (!workingAudio.analyser) {
          workingAudio.analyser = actx.createAnalyser();
          workingAudio.analyser.fftSize = 2 * barNum;
        }
        var analyser = workingAudio.analyser;

        if (!workingAudio.mediaSrc) {
          workingAudio.mediaSrc = actx.createMediaElementSource(workingAudio);
        }
        var mediaSrc = workingAudio.mediaSrc;
        mediaSrc.connect(analyser);
        analyser.connect(gainNode);

        // 短线Y坐标位置
        var dashYPositions = [];

        function visualization() {
            var arr = new Uint8Array(analyser.frequencyBinCount);
            requestAnimationFrame = window.requestAnimationFrame ||
                                        window.webkitRequestAnimationFrame ||
                                        window.mozRequestAnimationFrame;
            function v() {
                analyser.getByteFrequencyData(arr);
                drawBars(arr);
                requestAnimationFrame(v);
            }
            requestAnimationFrame(v);
        }
        // 绘制柱状音乐可视化效果
        function drawBars(arr) {
            ctx.clearRect(0, 0, CW, CH);
            let w = CW / (barNum) + 3;
            for (let i = 0; i < barNum; i++) {
                let value = arr[i];
                let h = value / (300) * CH;
                if (dashYPositions.length < barNum) {
                    dashYPositions.push(h);
                }
                ctx.fillStyle = '#FFCC00';
                if (h < dashYPositions[i]) {
                    ctx.fillRect(i*w, CH-(--(dashYPositions[i]))-7, w*0.7, 1.5);
                } else {
                    ctx.fillRect(i*w, CH-h-7, w*0.7, 1.5);
                    dashYPositions[i] = h;
                }
                ctx.fillStyle = vm.lineGradient;
                ctx.fillRect(i*w, CH-h, w*0.7, h);

            }
        }
        visualization();
    },
    typeText(mystr, target, cb) {
      var vm = this;
      var mystrCopy = mystr;
      var len = mystr.length;
      var strArr = mystr.split('');
      var colors = ['#d00', '#00d', '#0d0', '#cc0', '#c0c', '#0cc', '#000'];
      var progress = 0;
      var timer = setInterval(() => {
        if (progress < len) {
          target.innerHTML = mystrCopy.substring(0, progress)
            + '<span id="cursor">' + ((progress & 1)?'_':'') + '</span>';

          var random = Math.floor(Math.random() * colors.length);
          $('#cursor').css('color', colors[random]);
          progress++;
        } else {
          target.innerHTML += '<br>';
          $('#cursor').hide();
          window.clearInterval(timer);
          cb && cb.call();
        }
      }, 100)
    },
    testSheetInput() {
      var vm = this
      $('.tip-txt').html('')
      vm.isUserTyping = true
    },
    testSheetBlur() {
      var vm = this
      vm.isUserTyping = false
    },
    // 播放自定义谱曲
    playTestSheet() {
      var vm = this
      vm.playTimers.forEach((timer) => {
        clearInterval(timer)
        timer = null
      })
      vm.playTimers.splice(0)
      playTestSheet(1)
      playTestSheet(2)
      function playTestSheet(id) {
        vm['testSheet' + id] = $.trim(vm['testSheet' + id])/**.replace(/\s+/g, '')**/
        vm['testSheet' + id] = vm['testSheet' + id].replace(/[，]/g, ',').replace(/[：]/g, ':')

        var sheetRegExp = /^1=[a-gA-G]:([0-7][<>]*\([0-9.]+\)\,\s*)*([0-7][<>]*\([0-9.]+\)\,*\s*)$/
        if (!sheetRegExp.test(vm['testSheet' + id])) {
          var msg = "提示：乐谱格式错误，请检查~"
          id == 1 && (msg = '提示：主旋律乐谱格式错误，请检查~')
          id == 2 && (msg = '提示：伴奏乐谱格式错误，请检查~')
          $('.tip-txt').html(msg)
          return
        }
        var rhythm = vm['testSheet' + id].split(':')[0]
        rhythm = (rhythm.substring(2)).toUpperCase()
        var sheet = vm['testSheet' + id].split(':')[1]
        sheet = sheet.split(',')
        vm.autoPlayMusic(vm.Rhythm[rhythm], sheet, vm.testSheetSpeed)
      }
    }
  }
}
</script>

<style lang="scss">
$dark: #333;
$black: #000;
$pblue: #1296db;
$pred: #ef496f;

@mixin anim500 {
  transition: all linear 500ms;
}

.onepiano-app-wrap {
    width: 100%;
    min-height: 100%;
    position: relative;
    .trade-mark {
      // display: none;  
      width: 300px;
      height: 60px;
      line-height: 60px;
      padding-left: 50px;
      margin-left: 5%;
      background: url('../../static/imgs/music_piano.png') no-repeat;
      background-size: 35px 35px;
      background-position: 0 center;
      .trade-mark-txt {
        font-size: 20px;
        font-weight: 500;
        color: $pblue;
        .trade-mark-txt--en {
          font-style: italic;
        }
      }
    }
    .random-lyric {
      // display: none;    
      width: 100%;
      padding: 20px 0 20px;
      font-size: 20px;
      line-height: 30px;
      text-align: center;
    }
    #audioEffectCanvas {
      display: none;
    }
    .piano-wrap {
      // display: none;
      width: 90%;
      margin: 20px auto;
      .piano-band {
        width: 100%;
        height: 35px;
        background: url('../../static/imgs/band2.png') no-repeat $black;
        background-size: 130px 35px;
        background-position: center center;
      }
      .piano-key-wrap {
        width: 100%;
        background: $dark;
        letter-spacing: -10px;
        overflow: hidden;
        position: relative;
        .piano-key {
          &:hover {
            cursor: pointer;
          }
        }
        .auto-key-active {
          background: #f5deb3 !important;
        }
        .wkey {
          display: inline-block;
          letter-spacing: normal;
          width: 2.775%;
          height: 100%;
          margin: 0 auto;
          background: linear-gradient(white 10%, rgb(251, 251, 251) 92%, rgb(220, 220, 220) 93%, white 97%);
          border: solid 1px $dark;
          border-radius: 0 0 5px 5px;
          position: relative;

          &:active {
            background: linear-gradient(#eee 10%, #ddd 60%, #bbb 93%, #ccc 97%);
          }
          .keytip {
            width: 100%;
            color: $dark;
            text-align: center;
            position: absolute;
            font-size: 14px;
            bottom: 5%;
            .keyname {
              margin-bottom: 5px;
            }
            .notename {
              color: $pblue;
            }
          }
        }
        .wkey-active {
          background: linear-gradient(#eee 10%, #ddd 60%, #bbb 93%, #ccc 97%);
        }

        .bkey-wrap {
          width: 20%;
          height: 0;
          position: absolute;
          top: 0;
        }
        .bkey-wrap1 {left: 0;}
        .bkey-wrap2 {left: 19.5%;}
        .bkey-wrap3 {left: 39%;}
        .bkey-wrap4 {left: 58.3%;}
        .bkey-wrap5 {left: 77.7%;}
        .bkey {
          display: inline-block;
          width: 10%;
          height: 70%;
          background: linear-gradient(#000 10%, rgb(86, 86, 86) 85%, #000 90%);
          border-radius: 0 0 3px 3px;
          position: absolute;
          top: 0;
          overflow: hidden;          
          &:active {
            background: linear-gradient(rgb(86, 86, 86) 10%, #000 90%, #222 100%);
          }
          .keytip {
            width: 100%;
            color: white;
            position: absolute;
            left: 0;
            bottom: 5%;
            font-size: 14px;
            overflow: hidden;
            .keyname {
              width: 100%;
              margin-left: -25%;
              text-align: center;
            }
          }
        }
        .bkey-active {
          background: linear-gradient(rgb(86, 86, 86) 10%, #000 90%, #222 100%);
        }
        .bkey:nth-child(1) {left: 9%;}
        .bkey:nth-child(2) {left: 23%;}
        .bkey:nth-child(3) {left: 50%;}
        .bkey:nth-child(4) {left: 65%;}
        .bkey:nth-child(5) {left: 79%;}
      }
    }

    .piano-options {
      width: 90%;
      margin: 20px auto;
      padding: 5px 0;
      display: flex;
      justify-content: center;
      align-items: center;
      position: relative;
      .option-item {
        height: 33px;
        line-height: 33px;
        margin: 0 10px;
        .van-switch {
          cursor: pointer;
        }
        .van-switch--on {
          background: $pblue;
        }
        span {
          display: inline-block;
          vertical-align: top;
          margin: 0 10px;
        }
      }
      // 自动钢琴曲
      .music-list-btn {
        position: absolute;
        height: 35px;
        line-height: 35px;
        right: 20px;
        top: 50%;
        transform: translateY(-50%);
        cursor: pointer;
        color: $dark;
        font-weight: bold;
        &:hover {
        }
        img {
          width: 32px;
          height: 32px;
          display: inline-block;
          vertical-align: top;          
        }
        span {
          display: inline-block;
          vertical-align: top;
          margin-left: 7px;
        }
      }
    }
    
    // 自动钢琴曲列表
    .music-list {
      width: 30%;
      height: 100%;
      padding: 10px;
      background: linear-gradient(rgba(200,200,200,0.9) 30%, rgba(220,220,220,0.8)  50%, rgba(230,230,230,0.8)  100%);
      z-index: 200;
      .music-list__title {
        margin: 20px 0;
        color: $dark;
        .van-icon {
          padding-right: 10px;
        }
        .music-list__close {
          float: right;
          color: $dark;
          cursor: pointer;
          &:hover {
            color: $pred;
          }
