      }
      .music-list__item {
        cursor: pointer;
        &:hover {
          color: $pblue;
        }
      }
    }
    // 自动钢琴曲列表遮罩    
    .music-list-mask.user-involve {
      width: 70%;
      height: 100%;
      background: rgba(77, 77, 77, 1);
      border-right: solid 1px #fff;
      z-index: 100;

      .user-involve-wrap {
        width: 90%;
        height: 100%;
        margin: 0 auto;
        padding: 20px 0;
        color: #eee;
        overflow: scroll;
        .title {
          margin: 30px 0 20px;
        }
        .content {
          text-indent: 32px;
          line-height: 25px;
          margin: 10px 0;
        }
        .picture {
          width: 80%;
          margin: 0 auto;
        }
        .textarea {
          width: 100%;
          padding: 7px;
          margin: 10px 0;
          background: #ddd;
        }
        .play-btn {
          width: 100px;
          height: 35px;
          line-height: 35px;
          text-align: center;
          background: $pblue;
          color: white;
          border: none;
          cursor: pointer;
          &:hover {
            opacity: .85;
          }
        }
        .tip-txt {
          color: $pred;
          margin: 10px 0;
        }
      }
    }
    span.code {
      padding: 0 5px;
      margin: 0 5px;
      color: $pred;
      background: #eee;
      text-align: center;
    }
}

</style>



